@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;800&family=Inter:wght@300;400;600&display=swap');

:root {
  --bg: #070707;
  --surface: #0f0f10;
  --card-bg: rgba(17,17,17,0.65);
  --primary: #ff2d2d; /* neon red */
  --accent: #ff6161;
  --muted: #bdbdbd;
  --glass: rgba(255,255,255,0.03);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body, #__next {
  height: 100%;
}

body {
  font-family: 'Poppins', 'Inter', system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial;
  background:
    radial-gradient(1600px 800px at 10% 10%, rgba(255, 45, 45, 0.20), transparent 70%),
    radial-gradient(1400px 700px at 90% 90%, rgba(255, 45, 45, 0.20), transparent 80%),
    var(--bg);
  background-blend-mode: soft-light;
  color: #e6e6e6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.6;
  margin-top: 80px;
}

a {
  color: var(--accent);
  text-decoration: none;
  transition: color 0.2s ease;
}

a:hover {
  color: var(--primary);
}

.container { width: min(1200px, 90%); margin: 0 auto;
}

header, .header {
  background: linear-gradient(180deg, rgba(7,7,7,0.98), rgba(7,7,7,0.95));
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  padding: 1rem 0;
  border-bottom: 1px solid rgba(255,45,45,0.1);
  backdrop-filter: blur(10px);
}

.hero-section { padding: 6rem 0; text-align: center; position: relative; overflow: hidden; margin-top: 2rem;
}

.hero-description { margin-top: 0.75rem; color: rgba(255,255,255,0.8); font-size: 1.125rem; margin-bottom: 1.5rem; line-height: 1.6;
}

.hero-cta-button { display: inline-block; margin-top: 1.75rem; padding: 0.9rem 2rem; border-radius: 999px; background: transparent; color: #fff; border: 2px solid rgba(255,45,45,0.2); box-shadow: 0 0 20px rgba(255,45,45,0.06); text-decoration: none; transition: all 0.3s ease;
}

/* Neon card style used for pricing cards */

.pricing-card {
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border: 1px solid rgba(255,45,45,0.14);
  border-radius: 20px;
  padding: 2.5rem 2rem;
  color: #fff;
  box-shadow: 0 8px 30px rgba(0,0,0,0.7), 0 0 30px rgba(255,45,45,0.06) inset;
  position: relative;
  overflow: hidden;
}

.pricing-card::after {
  content: '';
  position: absolute;
  inset: -2px;
  background: linear-gradient(90deg, transparent, rgba(255,45,45,0.08), transparent);
  filter: blur(12px);
  z-index: 0;
  pointer-events: none;
}

.pricing-card > * { position: relative; z-index: 1;
}

.pricing-title { font-size: 1.5rem; font-weight: 700; color: #ffefef; margin-bottom: 1rem; line-height: 1.3;
}

.pricing-price { font-size: 2.25rem; color: var(--primary); font-weight: 800; margin: 1.5rem 0; line-height: 1.1;
}

.pricing-features { margin-bottom: 1.75rem; color: var(--muted);
}

.pricing-features li { margin-bottom: 0.5rem;
}

.btn-primary {
  background: linear-gradient(90deg, var(--primary), var(--accent));
  color: #0b0b0b;
  padding: 0.75rem 1.75rem;
  border-radius: 999px;
  font-weight: 600;
  font-size: 0.95rem;
  box-shadow: 0 8px 30px rgba(255,45,45,0.12);
  text-decoration: none;
  display: inline-block;
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
}

.btn-primary:hover {
  box-shadow: 0 12px 40px rgba(255,45,45,0.2);
  transform: translateY(-2px);
}

/* Modal Styles */

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  overflow: hidden;
}

.modal-content {
  background: linear-gradient(135deg, rgba(17,17,17,0.95), rgba(30,30,35,0.95));
  border: 1px solid rgba(255,45,45,0.2);
  border-radius: 20px;
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 20px 60px rgba(0,0,0,0.8);
  scrollbar-width: thin;
  scrollbar-color: rgba(255,45,45,0.3) transparent;
}

/* Webkit scrollbar (Chrome, Safari, Edge) */

.modal-content::-webkit-scrollbar {
  width: 6px;
}

.modal-content::-webkit-scrollbar-track {
  background: transparent;
}

.modal-content::-webkit-scrollbar-thumb {
  background: rgba(255,45,45,0.2);
  border-radius: 3px;
}

.modal-content::-webkit-scrollbar-thumb:hover {
  background: rgba(255,45,45,0.3);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  border-bottom: 1px solid rgba(255,45,45,0.1);
}

.modal-header h2 {
  margin: 0;
  color: #fff;
  font-size: 1.75rem;
}

.modal-close {
  background: none;
  border: none;
  color: rgba(255,255,255,0.6);
  font-size: 1.5rem;
  cursor: pointer;
  transition: color 0.2s ease;
}

.modal-close:hover {
  color: var(--accent);
}

.modal-body {
  padding: 2rem;
}

.modal-body h3 {
  color: var(--primary);
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

.modal-body h4 {
  color: #fff;
  font-size: 1.1rem;
  margin-top: 1.5rem;
  margin-bottom: 1rem;
}

.modal-body ul {
  list-style: none;
  padding-left: 0;
  margin-bottom: 1.5rem;
}

.modal-body li {
  color: var(--muted);
  padding: 0.5rem 0;
  padding-left: 1.5rem;
  position: relative;
}

.modal-body li:before {
  content: "✓";
  position: absolute;
  left: 0;
  color: var(--accent);
  font-weight: bold;
}

.modal-body p {
  color: rgba(255,255,255,0.8);
  line-height: 1.6;
}

.modal-footer {
  display: flex;
  gap: 1rem;
  padding: 2rem;
  border-top: 1px solid rgba(255,45,45,0.1);
}

.btn-secondary {
  background: rgba(255,255,255,0.05);
  color: rgba(255,255,255,0.8);
  padding: 0.75rem 1.75rem;
  border-radius: 999px;
  font-weight: 600;
  border: 1px solid rgba(255,255,255,0.1);
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s ease;
  flex: 1;
}

.btn-secondary:hover {
  background: rgba(255,255,255,0.1);
  border-color: rgba(255,255,255,0.2);
}

.modal-footer .btn-primary {
  flex: 1;
}

@media (max-width: 600px) {
  .modal-content {
    width: 95%;
  }

  .modal-footer {
    flex-direction: column;
  }
}

.nav-link { color: rgba(255,255,255,0.85); transition: color .15s;
}

.nav-link:hover { color: var(--accent);
}

footer, .footer { background: transparent; color: rgba(255,255,255,0.6);
}

/* Small utilities */

.glass { background: var(--glass); border-radius: 12px;
}

@media (min-width: 768px) {
  .hero-title { font-size: 4rem;
  }
}

/* Sobre Container Styles */

.sobre-container {
  padding: 2rem;
}

.sobre-container h1 {
  font-size: 2.5rem;
  font-weight: 800;
  color: #fff;
  margin-bottom: 2rem;
  letter-spacing: -0.02em;
}

.sobre-container h2 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #fff;
  margin-top: 3rem;
  margin-bottom: 1rem;
  padding-bottom: 0.75rem;
  border-bottom: 2px solid rgba(255,45,45,0.2);
  display: inline-block;
}

.sobre-container p {
  color: rgba(255,255,255,0.8);
  line-height: 1.8;
  font-size: 1.05rem;
  margin-bottom: 1.5rem;
  text-align: justify;
}