.hero-section {
  position: relative;
  overflow: hidden;
}

.hero-section > div:first-child {
  position: absolute;
  inset: 0;
  z-index: 0; /* garante que fique atrás do texto */
  pointer-events: none; /* evita clique no SVG */
}

.hero-background {
  position: absolute; inset: 0; z-index: 0; pointer-events: none;
  background: radial-gradient(600px 200px at 50% 40%, rgba(255,45,45,0.12), transparent);
}

.plans-container {
  padding: 4rem 0;
  padding-left: 2rem;
  padding-right: 2rem;
  margin: 0 auto;
  max-width: 1200px;
}

.section-title {
  font-size: 2.75rem;
  font-weight: 800;
  text-align: center;
  margin-bottom: 3.5rem;
  color: #fff;
  letter-spacing: -0.02em;
  text-transform: uppercase;
}

.plans-grid { display: grid; gap: 2rem; grid-template-columns: 1fr; align-items: stretch;
}

@media (min-width: 768px) {
  .plans-grid { grid-template-columns: repeat(3, 1fr); gap: 2.5rem;
  }
}

.pricing-card { width: 100%; min-height: 360px; display: flex; flex-direction: column; justify-content: space-between;
}

.pricing-card .price-cta { margin-top: 1rem; display: flex; justify-content: center;
}

.features-list { list-style: none; padding-left: 0; margin: 0;
}

.hero-title-centered { text-align: center; z-index: 10; position: relative; max-width: 900px; margin: 0 auto;
}

.header .container { display: flex; align-items: center; justify-content: space-between;
}

/* header inner layout */

.header-inner { display: flex; align-items: center; justify-content: space-between; padding: 0; width: 100%;
}

.site-logo { font-size: 0.0rem; font-weight: 800; letter-spacing: -0.05em;
}

.logo-image {
  height: 40px; /* Adjust height as needed */
  width: auto;
  max-width: 150px; /* Adjust max-width as needed */
}

.nav-list { list-style: none; display: flex; gap: 2.5rem; align-items: center; margin: 0; padding: 0;
}

.nav-item a { color: rgba(255,255,255,0.85); font-size: 0.95rem; font-weight: 500;
}

.nav-item a:hover { color: var(--accent);
}

.nav-item.active a {
  background: linear-gradient(90deg, var(--primary), var(--accent));
  color: #0b0b0b;
  padding: 0.75rem 1.75rem;
  border-radius: 999px;
  display: inline-block;
  font-weight: 600;
  box-shadow: 0 8px 30px rgba(255,45,45,0.12), 0 0 20px rgba(255, 0, 0, 0.179);
  transform: scale(1.05);
  transition: all 0.3s ease;
}

.nav-item.active a:hover {
  box-shadow: 0 10px 40px rgba(255, 0, 0, 0.179);
  transform: scale(1.08);
}

.nav-cta { margin-left: 1.5rem;
}

.nav-cta .btn-primary {
  background: transparent;
  color: rgba(255,255,255,0.85);
  padding: 0.5rem 1rem;
  border-radius: 999px;
  font-weight: 500;
  font-size: 0.95rem;
  box-shadow: none;
  border: none;
  transition: all 0.3s ease;
}

.nav-cta .btn-primary:hover {
  box-shadow: none;
  transform: none;
  color: var(--accent);
}

.nav-cta.active .btn-primary {
  background: linear-gradient(90deg, var(--primary), var(--accent));
  color: #0b0b0b;
  padding: 0.75rem 1.75rem;
  font-weight: 600;
  box-shadow: 0 8px 30px rgba(255,45,45,0.12), 0 0 20px rgba(255, 0, 0, 0.179);
  transform: scale(1.05);
}

.footer .container { display: flex; flex-direction: column; align-items: center;
}

.footer-links { display: flex; gap: 1.25rem; align-items: center; margin-top: 1rem; justify-content: center;
}

.divider { color: rgba(255,255,255,0.2);
}

/* Testimonials section */

.testimonials { padding: 4rem 0;
}

.testimonial-list { display: grid; gap: 2rem; grid-template-columns: 1fr;
}

@media (min-width: 768px) {
  .testimonial-list { grid-template-columns: repeat(3, 1fr);
  }
}

.testimonial-item {
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border: 1px solid rgba(255,45,45,0.12);
  border-radius: 16px;
  padding: 2rem 1.5rem;
  transition: all 0.3s ease;
}

.testimonial-item:hover {
  border-color: rgba(255,45,45,0.25);
  box-shadow: 0 8px 25px rgba(255, 0, 0, 0.179);
}

.testimonial-content { text-align: center;
}

.testimonial-feedback { color: rgba(255,255,255,0.85); font-size: 0.95rem; line-height: 1.6; margin-bottom: 1rem; font-style: italic;
}

.testimonial-name { color: #ffefef; font-weight: 600; margin-bottom: 0.5rem;
}

.testimonial-rating { color: var(--accent); font-size: 0.9rem;
}

/* features list items */

.feature-item { color: var(--muted); margin-bottom: 0.75rem; line-height: 1.5; font-size: 0.95rem;
}

.feature-empty { color: rgba(255,255,255,0.45); margin-bottom: 0.75rem;
}

/* Features section */

.features { padding: 4rem 0;
}

.features-grid { display: grid; gap: 2rem; grid-template-columns: 1fr;
}

@media (min-width: 768px) {
  .features-grid { grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1024px) {
  .features-grid { grid-template-columns: repeat(4, 1fr);
  }
}

.feature-card {
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border: 1px solid rgba(255,45,45,0.12);
  border-radius: 16px;
  padding: 2rem 1.5rem;
  text-align: center;
  transition: all 0.3s ease;
}

.feature-card:hover {
  border-color: rgba(242, 12, 12, 0.25);
  box-shadow: 0 8px 25px rgba(255, 0, 0, 0.179);
}

.feature-icon { font-size: 2.5rem; margin-bottom: 1rem;
}

.feature-card h3 { font-size: 1.2rem; font-weight: 600; margin-bottom: 0.75rem; color: #fff;
}

.feature-card p { color: var(--muted); font-size: 0.9rem; line-height: 1.5;
}

.hero-cta-button {
  z-index: 10;
  border-radius: 999px;
  padding: 0.9rem 2rem;
  background-color: rgba(255, 0, 0, 0.179);
  color: #ffffff;
  backdrop-filter: blur(4px);
  margin-top: 5rem;
}

.hero-cta-button:hover {
  background-color: rgba(239, 68, 68, 0.3); /* um pouco mais forte no hover */
  transform: scale(1.03);
  box-shadow: 0 0 20px rgba(255, 0, 0, 0.179);
}

.center { text-align: center;
}

.header .container { display: flex; align-items: center; justify-content: space-between;
}

.nav-link { margin-left: 1rem;
}

.footer .container { display: flex; flex-direction: column; align-items: center;
}

.glass-card { background: rgba(255,255,255,0.02); border-radius: 16px; padding: 1rem;
}

.center { text-align: center;
}